
"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import Link from "next/link";
import {
  Users,
  Store,
  ClipboardList,
  IndianRupee
} from "lucide-react";

export default function AdminDashboard() {
  const [users, setUsers] = useState<any[]>([]);
  const [restaurants, setRestaurants] = useState<any[]>([]);
  const [ orders, setOrders] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadDashboard = async () => {
      try {
        const [usersRes, restRes, orderRes] = await Promise.all([
          api.get("/users/all"),
          api.get("/restaurants"),
          api.get("/orders"),
        ]);
        console.log("userdatass",usersRes.data);
        console.log("resdata",restRes.data);
        console.log("orderdata",orderRes.data);
        setUsers(usersRes.data);
        setRestaurants(restRes.data);
        setOrders(orderRes.data);
      } catch (err) {
        console.error("Admin dashboard load failed", err);
      } finally {
        setLoading(false);
      }
    };

    loadDashboard();
  }, []);

  if (loading) return <p className="p-10 text-xl">Loading dashboard...</p>;

  const totalRevenue = orders.reduce(
    (sum, o) => sum + (o.totalPrice || 0),
    0
  );

  return (
    <div className="max-w-7xl mx-auto p-6">
      {/* Title */}
      <h1 className="text-4xl font-bold text-black mb-8">
        Admin Dashboard
      </h1>

      {/* Stats */}
      <div className="grid md:grid-cols-4 gap-6 mb-10">
        <StatCard
          icon={<Users className="w-10 h-10 text-orange-600" />}
          title="Users"
          value={users.length}
        />
        <StatCard
          icon={<Store className="w-10 h-10 text-orange-600" />}
          title="Restaurants"
          value={restaurants.length}
        />
        <StatCard
          icon={<ClipboardList className="w-10 h-10 text-orange-600" />}
          title="Orders"
          value={orders.length}
        />
        <StatCard
          icon={<IndianRupee className="w-10 h-10 text-orange-600" />}
          title="Revenue"
          value={`â‚¹ ${totalRevenue}`}
        />
      </div>

      {/* Quick Actions */}
      <h2 className="text-2xl font-bold text-black mb-4">
        Quick Management
      </h2>

      <div className="grid md:grid-cols-3 gap-6">
        <AdminLink
          href="/admin/users"
          title="Manage Users"
          icon={<Users className="w-12 h-12 text-orange-600" />}
        />
        <AdminLink
          href="dashboard/admin/restaurants"
          title="Manage Restaurants"
          icon={<Store className="w-12 h-12 text-orange-600" />}
        />
        <AdminLink
          href="/admin/orders"
          title="View Orders"
          icon={<ClipboardList className="w-12 h-12 text-orange-600" />}
        />
      </div>
    </div>
  );
}

/* ----------------- Reusable Components ----------------- */

function StatCard({ icon, title, value }: any) {
  return (
    <div className="bg-white border shadow-sm rounded-xl p-6">
      {icon}
      <h3 className="text-xl font-bold text-black mt-3">{title}</h3>
      <p className="text-2xl font-semibold text-black mt-1">{value}</p>
    </div>
  );
}

function AdminLink({ href, title, icon }: any) {
  return (
    <Link
      href={href}
      className="bg-white border shadow-md p-6 rounded-xl hover:shadow-lg transition flex flex-col items-center"
    >
      {icon}
      <span className="text-xl font-semibold text-black mt-3">
        {title}
      </span>
    </Link>
  );
}
